/* -------------------------------------------------- */
/*  Horizontal List */
/* -------------------------------------------------- */

.hl {
  font-size: 0;
}

.hl > .hli {
  font-size: 16px;
  display: inline-block;
  vertical-align: top;
}

.hl--bgrid > .hli {
  margin-right: 15px;
}

.hl--bgrid > .hli:last-child {
  margin-right: 0;
}

.hl-eq {
  display: table;
