}

.pos-rel {
  position: relative;
}

/* -------------------------------------------------- */
/* Flex Center  */
/* -------------------------------------------------- */

.cntr {
  display: flex;
  justify-content: center;
}

.cntr__itm {
  align-self: center;
}

/* -------------------------------------------------- */
/* Text ellipsis */
/* -------------------------------------------------- */

.txt-ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.pttl {
  position: relative;
  white-space: nowrap;
  overflow: hidden;
}

.pttl--e {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 60px;
  height: 24px;
  max-height: 100%;
  background-image: -webkit-linear-gradient(left, rgba(255, 255, 255, 0), #fff);
  background: linear-gradient(to right, rgba(255, 255, 255, 0), #fff);
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=1, StartColorStr=' #00ffffff ', EndColorStr=' #ffffff ');
}

/* -------------------------------------------------- */
/* separator with text */
/* -------------------------------------------------- */

.hr-text {
  line-height: 1em;
  position: relative;
  outline: 0;
  border: 0;
  color: black;
  text-align: center;
  height: 1.5em;
  opacity: 0.5;
}

.hr-text:before {
  content: "";
  background: linear-gradient(to right, transparent, #818078, transparent);
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 1px;
}

.hr-text:after {
  content: attr(data-content);
  position: relative;
  display: inline-block;
  padding: 0 0.5em;
  line-height: 1.5em;
  color: #51505d;
  background-color: #f1f1f2;
}

/* -------------------------------------------------- */
/* separator*/
/* -------------------------------------------------- */

.hr-sprtr {
  height: 1px;
  background-color: #dfe0e3;
  width: 15%;
  margin: 0 auto;
}

/* -------------------------------------------------- */
/* Circular Image List */
/* -------------------------------------------------- */

.circle-img-list {
  /*padding: 10px 0;*/
}

.circle-img-list-itm {
  position: relative;
}

.circle-img-list-itm-n {
  position: absolute;
  top: 0;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.3);
  color: #fff;
}

.circle-img-list-itm-n-txt {
  position: absolute;
  top: 20px;
  left: 20px;
}

.circle-img-list-itm-0 {
  padding-left: 10px;
}

.circle-img-list-itm-1 {
  left: -10px;
}

.circle-img-list-itm-2 {
  left: -20px;
}

.circle-img-list-itm img {
  border-radius: 50%;
  height: 60px;
  width: 60px;
}

/* -------------------------------------------------- */
/* Vertical and Horizontal page center alignment css*/
/* -------------------------------------------------- */

.hr-vr-cntr {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* -------------------------------------------------- */
/* hover link animation css*/
/* -------------------------------------------------- */

.hover-anim::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: #707070;
  transition: width 0.3s;
}

.hover-anim:hover::after {
  width: 100%;
  transition: width 0.3 s;
}

